:root {
    --white : #f5f5f5 ;
    --black : #0f0f0f ;
    --main-color : #00247f;
    --Pra-color : #29e8fc ;
    --main-font : 'Cairo', sans-serif ;
    --pra-font : 'Maven Pro', sans-serif;
    --main-transition : 0.8s ;
}

* {
    padding: 0;
    margin : 0;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

body {
    font-family: var(--main-font);
    background-color: var(--main-color);
    color: var(--Pra-color);
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}

h3 {
    line-height: 1.3;
}

/* Scrollbar */

::-webkit-scrollbar {
    width: 8px;
}

::-webkit-scrollbar-thumb {
    background-color: var(--Pra-color);
}

/* ######## header ######## */
#header {
    position: absolute;
    width: 0px;
    margin: 0 auto;
    z-index: 10;
    overflow: hidden;
    transition: var(--main-transition);
}

.contener {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    width: 90%;
    z-index: 9;

}

.contener .logo {
    display: flex;
    align-items: center;
    justify-content: center;
}

.contener .logo img {
    width: 30px;
    margin: 0 5px;
}

.contener .logo h2 {
    font-size: 2rem;
}

.contener .logo h2 span{
    font-size: 1.4rem;
    color: var(--white);
    font-weight: 200;
    transition: var(--main-transition);
}

.contener .logo:hover span{
    margin: 10px;
}

.contener .menu {
    z-index: 9;
    min-height: 58.89px;
}

.contener .menu #phmenu {
    position: absolute;
    top: 20px;
    right: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    display: none;
    z-index: 10;
}

.contener .menu #phmenu span{
    width: 25px;
    height: 3px;
    background-color: var(--white);
    margin: 3px 0;
    border-radius: 20px;
    transition: var(--main-transition);
}
.contener .menu #phmenu span:nth-child(1){
    width: 12.5px;
}
.contener .menu #phmenu span:nth-child(3){
    width: 12.5px;
}

.contener .menu ul {
    display: flex;
    justify-content: center;
    align-items: center;
    transition: var(--main-transition);
}

.contener .menu ul li {
    position: relative;
    padding: 10px 0 0px;
    margin: 0 10px;
    overflow: hidden;
}

.contener .menu ul li::after{
    content: "";
    position: absolute;
    width: 80%;
    height: 2px;
    border-radius: 20px;
    background-color: var(--white);
    bottom: 0;
    left: -100%;
    transition: var(--main-transition);
}

.contener .menu ul li:hover::after{
    left: 0;
    box-shadow: 0px 0px 20px 2px var(--white);
}

.contener .menu ul li a {
    position: relative;
    font-size: 1.3rem;
    color: var(--white);
}

@media (max-width: 767px) {
    .contener .menu ul {
        position: absolute;
        top: calc(300% + 20px);
        left: 50%;
        background-color: #082183e5;
        transform: translate(-50% , -50%);
        width: 0%;
        display: flex;
        flex-direction: column;
        overflow: hidden;
        /* display: none; */
        z-index: 9;
    }

    .contener .menu .show {
        width: 100%;
        padding: 20px 0;
    }


    .contener .menu #phmenu {
        display: flex;        
    }

    .contener .menu #phmenu .full {
        width: 25px !important;
    }
}

/* ######## Landing ######## */

#work {
    background-image: url(../../public/image/background.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 60px 0;
}

.landing {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

.landing .titleText {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 0 0 50px;
    opacity: 0;
    transition: var(--main-transition);
}

.landing .vis {
    margin: 0 0 50px;
    opacity: 1 !important;
}

.landing .titleText h1 {
    font-size: 2rem;
    max-width: 800px;
}

.landing .titleText p {
    font-size: 1.1rem;
    max-width: 500px;
    font-weight: 200;
    color: var(--white);
}

.landing .titleText a {
    padding: 5px 40px;
    margin: 10px 0;
    background: linear-gradient(165deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);
    font-size: 1rem;
    border-radius: 50px;
    color: var(--white);
    transition: var(--main-transition);
}

.landing .titleText a:hover {
    filter: drop-shadow(0 0 20px #29e8fc);
    background: linear-gradient(333deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);
}

@media (max-width : 767px) {
    .landing .titleText h1 {
        font-size: 1.4rem;
    }

    .landing .titleText p {
        font-size: 1rem;
        max-width: 400px;
    }

}

@media (max-width : 380px) {
    .landing .titleText h1 {
        font-size: 1.2rem;
    }

    .landing .titleText p {
        font-size: 0.8rem;
        max-width: 300px;
    }

}

/* ######## Services ######## */

#Services {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 100px 0;
}

.summery {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    padding: 30px;
    overflow: hidden;
    transition: var(--main-transition);
    opacity: 0;
}

.summery h3 {
    font-size: 2rem;
    max-width: 200px;
    margin: 0 50px;
    color: var(--white);
}

.summery p {
    margin: 0 50px;
    font-size: 1.3rem;
    font-weight: 200;
    color: var(--white);
    max-width: 600px;
}

.summery::after {
    content: "";
    position: absolute;
    width: 30%;
    height: 3px;
    bottom: 0;
    left: 0;
    background: linear-gradient(81deg,  var(--main-color) 11%, rgba(0,212,255,1) 100%);
}

.services {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: 30px;
}

.services .service {
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    max-width: 350px;
    margin: 0 50px ;
    transition: var(--main-transition);
    opacity: 0;
    transform: translateY(100px);
}

.services .show {
    opacity: 1;
    transform: translateY(0);
}

.services .service svg{
    width: 300px;
}

.services .service h4 {
    text-align: center;
    font-size: 1.6rem;
    font-weight: 500;
}

.services .service p {
    font-size: 1rem;
    text-align: center;
    max-width: 200px;
    color: var(--white);
}

#Services::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 300px;
    height: 300px;
    background-color: #ffffff13;
}

#Services::before {
    content: "";
    position: absolute;
    top: 50px;
    right: 130px;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    background: linear-gradient(35deg, rgba(0,0,0,0) 42%, rgba(41,232,252,1) 100%);
}

@media (max-width : 1200px) {
    .summery {
        display: flex;
        flex-direction: column;
        text-align: center;
        padding: 0 10px;
    }

    .services .service {
        margin: 50px;
    }
}

@media (max-width : 500px) {
    #Services::before {
        top: 50px;
        right: 40px;
    }

    .summery h3 {
        font-size: 1.4rem;
        max-width: 1000px;
        margin: auto;
    }
    .summery p {
        font-size: 1rem;
        margin: auto;
    }

    #Services::after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 200px;
        height: 200px;
        background-color: #ffffff13;
    }

}


/* ######## Projects ######## */

#projects {
    position: relative;
    overflow: hidden;
    padding: 50px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#projects .decor {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50% , -50%);
    z-index: -1;
    width: 100%;
    height: 100%;
}

#projects .titleText {
    padding: 30px;
    position: relative;

}

#projects .titleText::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 3px;
    bottom: 0;
    right: 50px;
    background: linear-gradient(81deg,  var(--main-color) 11%, rgba(0,212,255,1) 100%);
}

#projects .titleText h3 {
    font-size: 3rem;
    font-weight: 500;
    max-width: 500px;
    color: var(--white);
}

.projects {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.projects .project {
    position: relative;
    margin: 100px 0;
    transition: var(--main-transition);
    display: flex;
    z-index: 1;
}

.projects .project img {
    width: 1000px;
    margin: 0 50px 0 0;
}

.projects .project .descrip {
    position: absolute;
    bottom: -50px;
    right: -30px;
    background-color: #011947;
    width: 400px;
    height: 500px;
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.projects .project.left .descrip {
    left: -30px;
}

.projects .project.left img {
    margin: 0 0 0 50px;
}

.projects .project .descrip h4 {
    font-size: 1.8rem;
    margin: 20px 0;    
}

.projects .project .descrip p {
    font-size: 1.2rem;
    color: var(--white);
    text-align: start;
}

.projects .project a {
    position: absolute;
    bottom: -30px;
    left: 30%;
    transform: translate(-50% , 0) rotate(-30deg);
    color: var(--white);
    font-size: 1.4rem;
    transition: var(--main-transition);
}

.projects .project a::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50% , -50%);
    width: 80px;
    height: 80px;
    border-radius: 50%;
    border: 5px solid #1493fa;
    z-index: -1;
    transition: var(--main-transition);

}

.projects .project a::before {
    content: "";
    position: absolute;
    top: -30px;
    left: 50%;
    transform: translate(-50% , 0);
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #1493fa;
    transition: var(--main-transition);
}

.projects .project a:hover {
    transform: rotate(0);
    color: var(--main-color);
}

.projects .project a:hover::after {
    width: 150px;
    height: 40px;
    border-radius: 0;
}

.projects .project a:hover::before {
    top: 50%;
    left: 50%;
    transform: translate(-50% , -50%);
    width: 150px;
    height: 40px;
    border-radius: 0;
    z-index: -1;
}

#projects::after {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    width: 300px;
    height: 300px;
    background-color: #ffffff13;
}

#projects::before {
