/* ######## trust ######## */

#trust {
    background: rgb(241,241,241);
    background: linear-gradient(0deg, rgba(241,241,241,1) 51%, rgba(157,177,255,1) 100%);
    padding: 50px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#trust .text {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 20px;
}

#trust .text::after {
    content: "";
    position: absolute;
    width: 40%;
    height: 3px;
    bottom: 0;
    left: 0;
    background: linear-gradient(81deg,  rgba(0,212,255,1) 11% , var(--main-color) 100%);
}

#trust .text h3 {
    font-size: 2rem;
    color: var(--main-color);
    max-width: 380px;
    margin: 0 50px 0 0 ;
}

#trust .text p {
    font-size: 1.4rem;
    color: var(--black);
    max-width: 600px;
}

#trust .brands {
    margin: 100px 0 0 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 20px;
}

#trust .image {
    padding: 20px 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: calc(100% / 3);
    min-width: 300px;
    height: 200px;
    transition: var(--main-transition);
}

#trust .image:nth-child(1) {
    border-right: 1px solid gray;
    border-bottom: 1px solid gray;
}
#trust .image:nth-child(2) {
    border-right: 1px solid gray;
    border-bottom: 1px solid gray;
}
#trust .image:nth-child(3) {
    border-bottom: 1px solid gray;
}
#trust .image:nth-child(4) {
    border-right: 1px solid gray;
}
#trust .image:nth-child(5) {
    border-right: 1px solid gray;
}

#trust .brands .image img {
    width: 150px;
    filter: grayscale(100%);
    transition: var(--main-transition);
}

#trust .brands .image:hover img {
    filter: grayscale(0%);
    width: 170px;
}

@media (max-width : 927px) {
    
