/* ######## contact ######## */

#contact {
    background-position: right;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    background: linear-gradient(299deg, rgba(0,35,255,1) 0%, rgba(8,17,131,1) 68%);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 300px 200px 50px;
    position: relative;
    overflow: hidden;
}

#contact .reachus {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 250px;
}

#contact .reachus h3 {
    font-size: 2.5rem;
    color: var(--white);
    font-weight: 500;
    margin: 0 0 10px 0;
}

#contact .reachus a {
    padding: 5px 40px;
    margin: 10px 0;
    background: linear-gradient(165deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);
    font-size: 1rem;
    border-radius: 50px;
    color: var(--white);
    transition: var(--main-transition);   
}

#contact .reachus a:hover {
    filter: drop-shadow(0 0 20px #29e8fc);
    background: linear-gradient(333deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);    
}

#contact .social {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

#contact .social h4 {
    font-size: 2rem;
    font-weight: 500;
    color: var(--white);
    width: 100%;
}

#contact .social .icons {
    margin-top: 10px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

#contact .social .icons svg {
    width: 30px;
    margin: 0 10px;
}

#contact .social .icons img {
    width: 30px;
    margin: 0 10px;   
}

#contact .social .icons svg:hover {
    filter: drop-shadow(0 0 5px #29e8fc);

}

#contact .social .icons img:hover {
    filter: drop-shadow(0 0 5px #29e8fc);
}


#contact > .rocket {
    position: absolute;
    width: 800px;
    right: -50px;
    bottom: 10px;
}

.move {
    filter: drop-shadow(0 0 20px);
}

#contact > .planet {
    position: absolute;
    width: 200px;
    left: 30px;
    top: 50px;
    z-index: 1;
    filter: drop-shadow(0 0 20px);
}

@media (max-width : 1200px) {
    #contact {
        padding: 250px 100px 50px;
    }   

    #contact .rocket {
        width: 500px;
        right: 10px;
    }
}

@media (max-width: 767px) {
    #contact {
        padding: 250px 40px 50px;
    }   

    #contact .rocket {
        width: 300px;
        bottom: 150px;
    }
}

@media (max-width: 415px) {
    #contact {
        padding: 250px 40px 50px;
        align-items: center;
    }   

    #contact .reachus {
        align-items: center;
        text-align: center;
    }

    #contact .reachus h3 {
        font-size: 1.8rem;
    }

    #contact .social {
        align-items: center;
        text-align: center;
    }

    #contact .rocket {
        width: 300px;
        bottom: 150px;
        left: 50%;
        transform: translate(-50%);
    }
}